@import url('https://fonts.googleapis.com/css2?family=Libre+Baskerville:ital,wght@0,400;0,700;1,400&Playfair+Display:ital,wght@0,400..900;1,400..900&family=Montserrat:ital,wght@0,100..900;1,100..900&family=Anton&family=Noto+Sans+KR:wght@100..900&family=Roboto:ital,wght@0,100..900;1,100..900&display=swap');
@import url("https://cdn.jsdelivr.net/gh/orioncactus/pretendard@v1.3.9/dist/web/variable/pretendardvariable.min.css");

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
a{
  text-decoration: none;
}
li{
  list-style: none;
}
body,html{
  font-family: "Noto Sans KR", sans-serif;
  user-select: none;
}


html {
  font-size: 62.5%;
}

@media(max-width: 1300px) {
  html {
      font-size:58%;
  }
}

@media(max-width: 900px) {
  html {
      font-size:clamp(54%,1vw,58%);
  }
}

@media(max-width: 600px) {
  html {
      font-size:52%;
  }
}
body{overflow-x: hidden;}

.header{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  padding: 2rem;
  z-index: 9999;
  -webkit-backdrop-filter: blur(3px);
  backdrop-filter: blur(3px);
  img{
    display: block;
    width: 15rem;
  }
}

:root {
  --color-cursor: 247, 230, 0;
  --cursor-outline-shade: 0.3;
  --cursor-size: 10px;
  --cursor-outline-size: 12px;
}

#cursor_dot,
#cursor_dot_outline {
  z-index: 999;
  pointer-events: none;
  position: absolute;
  top: 50%;
  left: 50%;
  border-radius: 50%;
  opacity: 0;
  transform: translate(-50%, -50%);
  transition: opacity 0.15s ease-in-out, transform 0.15s ease-in-out;
  -webkit-backdrop-filter: invert(1);
  backdrop-filter: invert(1);
  transform-origin: center;

}

#cursor_dot {
  width: var(--cursor-size);
  height: var(--cursor-size);
  background-color: rgba(var(--color-cursor), 1);
}

#cursor_dot_outline {
  width: var(--cursor-outline-size);
  height: var(--cursor-outline-size);
  background-color: rgba(var(--color-cursor), var(--cursor-outline-shade));
}


.main_section01{ 
  background-color: #000;
  .main_section01_wrap{
    width: 100%;
    height: 100%;
    .main_section_video{
      position: relative;
      top: 0;
      left: 0;
      width: 100vw;
      height: 100vh;
      overflow: hidden;
      .main_video_box{
        width: 100vw;
        height: 100vh;
        top: 0;
        left: 0;
        position: absolute;
        display: flex;
        flex-direction: row;
        div{
          width: calc( 100% / 8 );
          background-color: #000;
          height: 100%;
        }
      }
      video{
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
    .main_text_right{
      position: absolute;
      top: 0;
      left: 0;
      mix-blend-mode: difference;
      z-index: 4;
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      width: 100%;
      height: 100%;
      .main_text_wrap{
        width: 100%;
        height: 14rem;
        margin-bottom: 1.2rem;
        overflow: hidden;
        .main_section_text01{
          position: relative;
          height: 14rem;
          width: 100%;
          z-index: 4;
          overflow: hidden;
          span{
            font-family: "Anton", sans-serif;
            display: inline-block;
            position: absolute;
            width: 100%;
            font-size: 14rem;
            font-weight: 800;
            line-height: 1;
            letter-spacing: 2px;
            text-align: center;
            color: #fff;
            mix-blend-mode: difference;
            &:hover{
              background-size: 100% 0%;
            }
          }
        }
      }
    }
  }
}


@keyframes ani {
	0% {
		stroke-dashoffset: 500px;  //0
	}
	50% {
		stroke-dashoffset: 0;
	}
	100% {
		stroke-dashoffset: -500px;
	}
}



.main_section02{
  min-height: 100vh;
  background-color: #000;
  position: relative;
  z-index: 100;
  .main_section02_wrap{
    position: relative;
    z-index: 99;
    .main_section02_scroll{
      display: block;
      position: absolute;
      top: 2.8rem;
      left: calc( 50% - 2.4rem );
      width: 4.8rem;
      font-size: 1rem;
      text-align: center;
      color: #fff;
      font-family: "Anton", sans-serif;
    }
    .main_section02_list{
      .main_section02_textbox01{
        .main_text_wrap{
          .main_text_wrap_top{
            padding-top: 10rem;
            .main_section02_text{
              overflow: hidden;
              &.main_section02_text_top{
                span{
                  font-family: "Anton", sans-serif;
                  display: inline-block;
                  font-size: 14rem;
                  font-weight: 700;
                  line-height: 1;
                  letter-spacing: 2px;
                  text-align: left;
                  padding-right: 2rem;
                  padding-left: 2rem;
                  -webkit-text-stroke-width: 0.1px;
                  -webkit-text-stroke-color: #fff;
                  -webkit-text-fill-color: transparent;
                  background-image: linear-gradient(#fff, #Fff);
                  background-size: 100% 100%;
                  background-position: bottom;
                  background-repeat: no-repeat;
                  background-clip: text;
                  -webkit-background-clip: text;
                  color: transparent;
                  transition: all 0.4s cubic-bezier(0.215, 0.61, 0.355, 1);
                }
              }
            }
          }
          .main_text_wrap_bottom{
            .main_section02_text{
              margin-top: 1.6rem;
              overflow: hidden;
              &.main_section02_text_bottom{
                span{
                  font-family: "Anton", sans-serif;
                  display: inline-block;
                  font-size: 14rem;
                  font-weight: 700;
                  line-height: 1;
                  letter-spacing: 2px;
                  text-align: left;
                  padding-right: 2rem;
                  padding-left: 2rem;
                  -webkit-text-stroke-width: 0.1px;
                  -webkit-text-stroke-color: #fff;
                  -webkit-text-fill-color: transparent;
                  background-image: linear-gradient(#fff, #Fff);
                  background-size: 100% 0%;
                  background-position: bottom;
                  background-repeat: no-repeat;
                  background-clip: text;
                  -webkit-background-clip: text;
                  color: transparent;
                  transition: all 0.4s cubic-bezier(0.215, 0.61, 0.355, 1);
                }
              }
            }
          }
          &:hover{
            .main_text_wrap_top{
              .main_section02_text{
                &.main_section02_text_top{
                  span{
                    background-size: 100% 0%;
                  }
                }
              }
            }
            .main_text_wrap_bottom{
              .main_section02_text{
                &.main_section02_text_bottom{
                  span{
                    background-size: 100% 100%;
                  }
                }
              }
            } 
          }
        }
      }
      .main_section02_textbox02{
        display: flex;
        flex-direction: row;
        align-items: flex-end;
        justify-content: flex-end;
        overflow: hidden;
        span{
          display: inline-block;
          width: 100%;
          font-size: 3.2rem;
          font-weight: 400;
          line-height: 1.3;
          text-align: center;
          padding: 8rem 0 15rem;
          color: #fff;
          padding-left: 1rem; 
          strong{
            font-size: 3.5rem;
            font-weight: 800;
          }
        }
      }
    }
  }
}

.main_section03{
  position: relative;
  z-index: 100;
  background-color: #000;
  overflow: hidden;
  padding-bottom: 0;
  .main_section03_bg{
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: #fff;
  }
  .section_title{
    display: block;
    margin: 15rem 0 15rem;
    text-align: center;
    line-height: 1.3;
    mix-blend-mode: difference;
    span{
      display: inline-block;
      color: #fff;
      font-family: "Montserrat", sans-serif;
      font-size: 9rem;
      font-weight: 800;
    }
  }
  .main_section03_Wrap{
    width: 100%;
    display: flex;
    flex-direction: row;
    gap: 5rem;
    align-items: center;
    justify-content: center;
    .main_section03_Swiper{
      flex: 1;
      width: 100%;
      max-width: 100%;
      overflow: hidden;
      position: relative;
      height: 100vh;
    }
    .main_section03_box{
      display: flex;
      flex: 1;
      flex-direction: column;
      justify-content: center;
      width: 100%;
      max-width: 100%;
      overflow: hidden;
      position: relative;
      height: 100vh;
      .main_section03_about{
        display: flex;
        flex-direction: column;
        align-items: flex-start ;
        justify-content: flex-start;
        .main_section03_aboutBox{
          display: flex;
          flex-direction: column;
          gap: 1rem;
          padding-right: 1rem;
          mix-blend-mode: difference;
          .main_section03_textbox{
            position: relative;
            display: flex;
            flex-direction: row;
            align-items: flex-start;
            justify-content: flex-start;
            flex-wrap: wrap;
            gap: 1rem;
            .main_section03_text{
              font-family: "Anton", sans-serif;
              display: block;
              overflow: hidden;
              font-size: 5.4rem;
              font-weight: 500;
              line-height: 1;
              letter-spacing: 5px;
              text-align: left;
              -webkit-text-stroke-width: 1px;
              -webkit-text-stroke-color: #fff;
              -webkit-text-fill-color: transparent;
              background-image: linear-gradient(#fff, #Fff);
              background-size: 100% 0%;
              background-position: bottom;
              background-repeat: no-repeat;
              background-clip: text;
              -webkit-background-clip: text;
              color: transparent;
              transition: all 0.4s cubic-bezier(0.215, 0.61, 0.355, 1);
              transition: all .5s ease-in;
              &.main_section03_textLeft{
                background-size: 100% 100%;
                background-position: left top;
                &:hover{
                  background-size: 100% 100%;
                }
              }
              &.main_section03_textCenter{
                background-size: 100% 0%;
                background-position: left top;
                &:hover{
                  background-size: 100% 100%;
                }
              }
              &.main_section03_textRight{
                background-size: 100% 100%;
                background-position: right top;
                &:hover{
                  background-size: 0% 100%;
                }
              }
              i{
                display: inline-block;
                font-style: normal;
                -webkit-text-stroke-width: 1px;
                -webkit-text-stroke-color: #fff;
                -webkit-text-fill-color: transparent;
                background-image: linear-gradient(#fff, #Fff);
                background-size: 100% 0%;
                background-position: bottom;
                background-repeat: no-repeat;
                background-clip: text;
                -webkit-background-clip: text;
                transition: all 0.4s cubic-bezier(0.215, 0.61, 0.355, 1);
                transition: all .5s ease-in;
              }
              &:hover{
                i{
                  background-size: 100% 100%;
                }
              }
            }
          }
        } 
        .mail_link{
          position: relative;
          display: inline-block;
          margin-top: 3rem;
          font-size: 1.6rem;
          mix-blend-mode: difference;
          div{
            display: flex;
            flex-direction: row;
            gap: 0.5rem;
            align-items: center;
            justify-content: flex-start;
            padding: 1rem 1.6rem;
            border: 2px solid #fff;
            border-radius: 5rem;
            span{
              font-size: 2.4rem;
              color: #fff;
              font-weight: 300;
            }
            img{
              display: block;
              width: 2.4rem;
            }
          }
        }
      }
    }
  }
}
.swiper-slide {
  background-position: center;
  background-size: cover;
}
img {
  display: block;
  object-fit: cover;
  width: 100%;
  height: 100%;
}


.main_section04{
  position: relative;
  z-index: 100;
  background-color: #fff;
  padding-bottom: 15rem;
  .main_section04_bg{
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: #000;
  }
  .section_title{
    display: block;
    text-align: center;
    line-height: 1.3;
    mix-blend-mode: difference;
    padding: 30rem 0 15rem;
    span{
      display: inline-block;
      color: #fff;
      font-family: "Montserrat", sans-serif;
      font-size: 9rem;
      font-weight: 800;
    }
  }
  .main_section04_portWrap{
    width: calc( 100% - 4rem );
    margin: 0 2rem;
    ul{
      li{
        position: relative;
        .main_section04_itemBack{
          position: absolute;
          z-index: 100;
          width: 100%;
          height: 100%;
          background-color: #000;
        }
        .main_section04_itemBox{
          display: flex;
          flex-direction: row;
          justify-content: flex-start;
          border-top: 1px solid #2a2a2a;
          border-bottom: 1px solid #2a2a2a;
          padding: 6rem;
          background-image: linear-gradient(#fff, #Fff);
          background-size: 100% 0%;
          background-position: bottom;
          background-repeat: no-repeat;
          transition: all 1s cubic-bezier(0.215, 0.61, 0.355, 1);
          &:hover{
            padding: 8rem 10rem;
            background-size: 100% 100%;
          }
          .main_section04_itemLeft{
            .main_section04_item_num{
              padding: 0 3rem;
              span{
                font-size: 4rem;
                color: #fff;
                font-weight: 900;
                line-height: 1.4;
                display: block;
                mix-blend-mode: difference;
              }
            }
          }
          .main_section04_itemCenter{
            display: flex;
            flex-direction: column;
            width: 70rem;
            gap: 3rem;
            padding: 0 3rem;
            .main_section04_item_title{
              span{
                font-size: 6rem;
                color: #fff;
                line-height: 1;
                display: block;
                font-weight: 700;
                word-break: keep-all;
                mix-blend-mode: difference;
              }
            }
            .main_section04_item_ment{
              span{
                font-size: 2.2rem;
                color: #fff;
                line-height: 1.3;
                display: block;
                font-weight: 400;
                word-break: keep-all;
                mix-blend-mode: difference;
              }
            }
            .main_section04_item_tag{
              display: flex;
              flex-direction: row;
              gap: 1rem;
              flex-wrap: wrap;
              span{
                font-size: 2rem;
                color: #fff;
                line-height: 1;
                display: block;
                font-weight: 500;
                mix-blend-mode: difference;
              }
            }
          }
          .main_section04_itemRight{
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
            margin-left: auto;
            gap: 2rem;
            .main_section04_item_link{
              a{
                display: block;
                font-size: 2.4rem;
                font-weight: 500;
                font-family: "Montserrat", sans-serif;
                color: #000;
                mix-blend-mode: difference;
                padding: 1rem 3rem;
                background: #fff;
                border-radius: 3rem;
                &:hover{
                  background: #fff;
                  color: #000;
                }
              }
            }
          }
        }
        &:last-child{
          .main_section04_itemBox{
            border-top: none;
          }
        }
      }
    }
  }
}


.main_section05{
  position: relative;
  z-index: 100;
  background-color: #000;
  padding: 16rem 0 10rem;
  min-height: 100vh;
  .main_section05_back{
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: #fff;
    top: 0;
    left: 0;
  }
  .section_hypen{
    position: absolute;
    height: 24rem;
    top: calc( 0px - 11rem );
    left: calc( 50% - 1px );
    display: block;
    width: 2px;
    transition: all .5s ease-in-out;
    mix-blend-mode: difference;
    div{
      display: block;
      width: 100%;
      height: 100%;
      background-color: #fff;
    }
  }
  .section_scroll_wrap{
    position: absolute;
    display: block;
    width: 5rem;
    z-index: 99;
    top: -2rem;
    left: calc( 50% + 2rem );
    mix-blend-mode: difference;
    div{
      display: block;
      width: 5rem;
      height: 5rem;
      img{
        display: block;
        width: 5rem;
        height: 5rem;
      }
    }
  }
  .main_section05_wrap{
    mix-blend-mode: difference;
    .main_section05_text{
      overflow: hidden;
      font-size: 4rem;
      color: #fff;
      line-height: 1.1;
      margin: 2rem 10rem 10rem 10rem;
      text-align: right;
      span{
        display: block;
        width: 100%;
        font-family: "Anton", sans-serif;
        font-size: 14rem;
        font-weight: 700;
        line-height: 1;
        letter-spacing: 2px;
        text-align: right;
        padding-right: 1rem;
        padding-left: 1rem;
        -webkit-text-stroke-color: #fff;
        -webkit-text-fill-color: transparent;
        background-image: linear-gradient(#fff, #Fff);
        background-size: 100% 100%;
        background-position: bottom;
        background-repeat: no-repeat;
        background-clip: text;
        -webkit-background-clip: text;
        color: transparent;
        margin-bottom: 2rem;
        &:last-child{
          margin-bottom: 0;
        }
      }
    }
    .main_section05_cont{
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: flex-start;
      padding: 10rem 10rem;
      gap: 5rem;
      border-top: 1px solid #2a2a2a;
      .main_section05_info{
        mix-blend-mode: difference;
        text-align: left;
        display: flex;
        flex-direction: row;
        gap: 5rem;
        span{
          font-family: "Montserrat", sans-serif;
          font-size: 14rem;
          line-height: 1;
          color: #fff;
          font-weight: 500;
          &:nth-child(1){
            line-height: 0.85;
          }
        }
      }
      .main_section05_link{
        display: flex;
        flex-direction: row;
        gap: 2rem;
        overflow: hidden;
        .main_section05_linkItem{
          width: 16rem;
          height: 16rem;
          border-radius: 16rem;
          background-color: #fff ;
          mix-blend-mode: difference;
          border: 1px solid #fff;
          a{
            display: flex;
            width: 100%;
            height: 100%;
            align-items: center;
            justify-content: center;
            span{
              font-size: 2.4rem;
              font-weight: 700;
              color: #000;
              margin-right: 1rem;
            }
            img{
              display: block;
              width: 3rem;
              height: auto;
            }
          }
        }
      }
    }
  }
}

footer{
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 6rem;
  line-height: 6rem;
  text-align: center;
  mix-blend-mode: difference;
  div{
    display: block;
    width: 100%;
    height: 100%;
    span{
      font-size: 2rem;
      color: #fff;
    }
  }
}


